import { createSlice, PayloadAction } from '@reduxjs/toolkit';

import { AppThunk } from 'app/store';

interface OptionsState {
  showTimestamps: boolean;
  splitChat: boolean;
  blacklistKeywords: string;
  highlightKeywords: string;
  fixedWidth: boolean;
}

// interface InitializeOptions {
//   showTimestamps?: boolean;
//   splitChat?: boolean;
//   blacklistKeywords?: string;
//   highlightKeywords?: string;
//   fixedWidth?: boolean;
// }

interface ChangeOption {
  name: keyof OptionsState;
  value: any;
}

const readOptionsFromLocalStorage = () => {
  try {
    return JSON.parse(localStorage.options);
  } catch {
    return {};
  }
};

const writeOptionsToLocalStore = (options: object) => {
  localStorage.setItem(
    'options',
    JSON.stringify({
      ...readOptionsFromLocalStorage(),
      ...options,
    }),
  );
};

const initialState: OptionsState = {
  showTimestamps: false,
  splitChat: true,
  blacklistKeywords: '',
  highlightKeywords: '',
  fixedWidth: false,
  ...readOptionsFromLocalStorage(),
};

const options = createSlice({
  name: 'options',
  initialState,
  reducers: {
    // TODO use this action
    // initializeOptions: (state, {payload}: PayloadAction<InitializeOptions>) => ({
    //   ...state,
    //   ...payload,
    // }),

    changeOption: (state, { payload }: PayloadAction<ChangeOption>) => {
      const { name, value } = payload;
      (state[name] as unknown) = value;
    },
  },
});

// export const { initializeOptions } = options.actions;

export default options.reducer;

const { changeOption } = options.actions;

export const changeChatOption = ({
  name,
  value,
}: ChangeOption): AppThunk => async (dispatch) => {
  writeOptionsToLocalStore({ [name]: value });
  dispatch(changeOption({ name, value }));
};
